@import (less) "color.less";

@fontSizeTitle: 0.875em;

.panel {
    border: none !important;
    border-radius: 0 !important;
    box-shadow: none;
    .panel-open {
      background-color: @gray !important;
    }
}
.panel-heading {
    border: none !important;
    border-radius: 2px 2px 0px 0px !important;
    box-shadow: none;
    background-color: @light_gray !important;
    padding: 1em 1.250em 1em 1.250em !important;
    &:hover {
      background-color: @gray !important;
    }
    &.panel-heading-round{
      border-radius: 2px 2px 2px 2px !important;
    }
    .panel-title {
        color: @white;
        font-weight: bold;
        font-size: 0.875em !important;
        text-transform: uppercase;
    }
    .accordion-arrow {
        color: @white;
        position: relative;
        font-size: 2em;
        margin-top: -0.156em;
    }
    .stringa{
        color: @white;
        font-size: 0.875em !important;
        margin-right: 1.875em;
        font-weight: bold;
        font-family: arial;
    }   
    h4 {
        margin: 0;
        font-size: 1em;
    }
}

.panel-body {
    border: none !important;
    box-shadow: none;
    background-color: @lightest_gray !important;
    border-radius: 0px 0px 2px 2px !important;
}

  .panel-collapse{
    .panel-body;
     max-height: 0;
     transition: max-height 0.15s ease-out;
     overflow: hidden;
    &.in{
      max-height: 1000em;
      transition: max-height 0.25s ease-in;
    }
  }

.panelTitle{
      font-size: @fontSizeTitle;
      font-weight: bold;
      text-transform: uppercse;
}

.accordionIcon{
     font-size: 32px;
     position:absolute;
     top:9px;
}


.nbp-panel-primary {
  >.panel-heading {
      background-color: @green !important;
      position: relative;
      &:hover {
        background-color: @lighter_green !important;
      }
      & .accordion-icon{
          .accordionIcon
      }
      >.panel-title{
       .panelTitle;
        >.title{
          padding: 2px 2px 2px 0px;
        }
        .accordion-title-icon{
          padding: 2px 2px 2px 52px !important;
          margin-bottom:10px;
        }
    }
    >.panel-open {
      background-color: @lighter_green !important;
    }
  }
}

.panel-default{
  >.panel-heading {
      background-color: @light_gray !important;
      &:hover {
        background-color: @gray !important;
      }
      & .accordion-icon{
          .accordionIcon
      }
    >.panel-title{
        .panelTitle;
        >.title{
          padding: 2px 2px 2px 20px;
        }
        .accordion-title-icon{
          padding: 2px 2px 2px 52px !important;
          margin-bottom:10px;
        }
    }
  }
  >.panel-open{
    background-color: @gray !important;
  }

}

.contrasto-on {
    .panel {
        .panel-open {
          background-color: @white !important;
          color:@black;
          border: 1px solid @black !important;
          .panel-title {
            color: @black;
          }
          .stringa {
            color:@black;
          }
          .accordion-arrow {
            color:@black;
          }
        }
    }
    .panel-heading {
        background-color: @black !important;
        &:hover {
          background-color: @white !important;
          color: @black;
          border: 1px solid @black !important;
          .panel-title {
            color: @black;
          }
          .stringa {
            color:@black;
          }
          .accordion-arrow {
            color:@black;
          }
        }
        .panel-title {
            color: @white;
        }
        .accordion-arrow {
            color: @white;
        }
        .stringa{
            color: @white;
        }
    }

    .panel-body {
        background-color: @white !important;
        border-top: none !important;
        border-right: 2px solid @black !important;
        border-bottom: 2px solid @black !important;
        border-left: 2px solid @black !important;
        color:@black;
    }
    .accordion-green-header {
        >.panel-heading {
          background-color: @black !important;
          &:hover {
            background-color: @white !important;
            color:@black;
            border: 1px solid @black;
          }
        }
        >.panel-open {
          background-color: @white !important;
          color:@black;
          border:1px solid @black !important;
        }
    }
}
